<template>
  <b-container>
    <b-card header="Sign up"
            border-variant="secondary"
            header-border-variant="secondary"
            style="max-width: 23rem;"
            class="mx-auto">
      <b-alert :show="!!errorMessage" dismissible variant="danger"
        >{{ errorMessage }}</b-alert>
      <b-form @submit="signup">
        <b-form-group label="Name:" label-for="signupEmail">
          <b-form-input id="signupEmail"
                        type="text" v-model="name" required
                        placeholder="Enter name"
          ></b-form-input>
        </b-form-group>
        <b-form-group label="Email:" label-for="signupEmail">
          <b-form-input id="signupEmail"
                        type="email" v-model="email" required
                        placeholder="Enter email"
          ></b-form-input>
        </b-form-group>
        <b-form-group label="Password:" label-for="signupPassword">
          <b-form-input id="signupPassword"
                        type="password" v-model="password" required
                        placeholder="Enter password"
          ></b-form-input>
        </b-form-group>
        <b-form-group label="Invite code:" label-for="signupInviteCode">
          <b-form-input id="signupInviteCode"
                        type="inviteCode" v-model="inviteCode" required
                        placeholder="Enter invite code"
          ></b-form-input>
        </b-form-group>
        <b-row align-h="end">
          <b-col>
            <b-link  align-v="center" to="login">Log in</b-link>
          </b-col>
          <b-col cols="auto">
            <b-button type="submit" variant="primary">Sign up</b-button>
            <b-button type="reset" variant="secondary">Reset</b-button>
          </b-col>
        </b-row>
      </b-form>
    </b-card>
  </b-container>
</template>

<script>
import axios from 'axios'
import router from '@/router'
import store from '@/store'

export default {
  name: 'signup',
  data () {
    return {
      loader: false,
      errorMessage: null,
      name: null,
      email: null,
      password: null,
      inviteCode: null
    }
  },
  methods: {
    signup (evt) {
      this.errorMessage = null
      let self = this
      evt.preventDefault()
      const path = '/api/signup'
      axios.post(path, {
        email: this.email,
        name: this.name,
        password: this.password,
        inviteCode: this.inviteCode
      }).then(response => {
        const token = response.data.jwt
        const name = response.data.name
        const admin = response.data.admin
        if (token !== null && name !== null && admin !== null) {
          localStorage.setItem('token', token)
          localStorage.setItem('name', name)
          localStorage.setItem('admin', admin)
          store.commit('LOGIN_USER')
          router.push('/')
        } else {
          self.errorMessage = 'Signup failed. Please try again.'
        }
      }).catch(error => {
        if (error.response) {
          let response = error.response.data
          self.errorMessage = response.msg
        }
        if (!self.errorMessage) {
          self.errorMessage = error.message
        }
        console.log(error)
      })
    }
  }
}
</script>


